<template>
    <div class="bind-form">
        <div style="margin-bottom: 10px;">
         <input type="search" v-model="mobile" maxlength="11" placeholder="" class="mobile input">
        </div>
        <div style="display: flex;">
            <input type="text" v-model="captcha" placeholder="" class="captcha input">
            <span class="button captcha" @click="getCaptcha()" v-bind:class="{'active': validMobile}">{{captchaText}}</span>
        </div>
        <div class="button submit" @click="submit()" v-bind:class="{'active': validForm}"></div>
        <div v-if="error">{{error}}</div>
    </div>
</template>

<script>

export default {
  name: 'bindaccount',
  created(){
      this.$http.post('public/api/customer/get_user_info_by_token.html',{
        "token":this.$global.userInfo.token
      })
      .then(response => {
        if(response.data.data.mobile){
            this.$global.userInfo.mobile = response.data.data.mobile;
            this.$global.userInfo.more = response.data.data.more;
            this.$router.replace('myorder');
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
  computed:{
    validMobile(){
        return this.mobile && this.mobile.length === 11;
      },
      validForm(){
        return this.validMobile && this.captcha;
      },
        captchaText(){
          if(this.waiting){
              return this.waiting + '';
          }else{
              return '';
          }
      }
  },
  methods:{
      getCaptcha(){
        if(!this.validMobile || this.waiting){
            return;
        }
        this.$http.post('public/api/customer/sendSmsCode.html',{
            "token":this.$global.userInfo.token,
            "phone" : this.mobile
        }).then(() => {
            this.setWaiting();
        }).catch(error =>{
            this.$toast('');
        });
      },
      setWaiting(){
          this.waiting = 60;
          const timer = setInterval(()=>{
              if(this.waiting === 0){
                  clearInterval(timer);
                  return;
              }
              this.waiting --;
          },1000);
      },
      submit(){
            if(!this.validMobile){
                return;
            }
          this.$http.post('public/api/customer/bind.html',{
                "token":this.$global.userInfo.token,
                "phone" : this.mobile,
                "captcha" : this.captcha
            }).then((response)=>{
                this.$global.userInfo.mobile = this.mobile;
                var newToken = response.data.data.token;
                if(newToken && typeof newToken === 'string' && this.$global.userInfo.token !== newToken){
                    this.$global.userInfo.token = newToken;
                    localStorage.setItem('userInfo',JSON.stringify(this.$global.userInfo));
                }
                if(this.$global.selected.length){
                    this.$router.replace({name:'order'});
                }else{
                    this.$router.replace({name:'home'});
                }
            }).catch((error)=>{
                this.$toast(error.data.msg);
            });
      }
  },
  data() {
    return {
        mobile: '',
        captcha: '',
        waiting: 0,
        error: null
    }
  }
}
</script>

<style lang="scss" scoped>
  .bind-form{
      margin: 140px 38px 0 38px;
        ::-webkit-input-placeholder { 
            color: #c7c7c7;
        }
    .input {
        width: 300px;
        height: 52px;
        padding-left: 20PX;
        background-color: #f2f2f2;
        border: none;
        
        &.mobile{
            width: 100%;

        }


        &.captcha{
            width: 176px;
        }

    }

    .button{
        background-color: #d9d9d9;
        color: #ffffff;
        height: 52px;
        line-height: 52px;

        &.active{
            background-color: #f3c81f;
        }

        &.captcha{
            width: 115px;
            font-size: 16px;
            margin-left: 10px;
        }
        &.submit{
            width: 100%;
            font-size: 18px;
            margin-top: 40px;
        }

        
    }
 
  }


</style>
