<template>
      <van-popup v-model="show" position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @confirm="onConfirm"
          @cancel="cancle"
          @change="onChange"
        />
      </van-popup>
</template>
<script>
const today = new Date();
export default {
  name: 'DatePicker',
  data(){
    return{
      currentValues: [],
      years: this.range(today.getFullYear(),3),
      months: this.range(1,12),
      days:[],
      extend:['',''],
      columns:[
      ],
      show: false
    }
  },
  created(){
    this.days = this.getDays(this.years[0],this.months[0]);
    this.columns = [
        {values: this.years},
        {values: this.months},
        {values: this.days},
        {values: this.extend}
    ],
    this.currentValues = [this.columns[0],this.columns[1],this.columns[2],this.columns[3]]
  },
  methods:{
    open(){
      this.show = true;
    },
    close(){
      this.show = false;
    },
    getDays(year,month){
    var days =[];
    //2
    if(month == 2){
        //
        if((year % 4 === 0 && year % 100 !== 0)  || year % 400 === 0){
            days = this.range(1,29);
        //
        }else{
            days = this.range(1,28);
        }
    //46911
    }else if(month == 4 || month == 6 ||month == 9 ||month == 11){
        days = this.range(1,30);
    }else{
        days = this.range(1,31);
    }
    return days;
},
    range(start,length){
      let arr = [];
      let item = start;
      for(let i = 0; i < length; i++){
        arr.push(item.toString().padStart(2, "0"));
        item ++ ;
      }
      return arr;
    },
    onChange(picker, values) {
      if(this.currentValues[0] !== values[0] || this.currentValues[1] !== values[1]){
        let currentDay = values[2];
        let days = this.getDays(values[0],values[1]);
        picker.setColumnValues(2, days);
        if(days.includes(currentDay)){
          this.$nextTick(()=>{
            picker.setColumnValue(2,currentDay)
          });
        }
      }
      this.currentValues = values;
      this.$emit('change',values);
    },
    onConfirm(){
      this.close();
      this.$emit('confirm',this.currentValues);
    },
    cancle(){
      this.close();
      this.$emit('cancel');
    }
  }
}
</script>
