<template>
  <div class="page order">
    <div class="order-head">
      <div class="order-head-text"></div>
    </div>
    <div class="order-section top">
      <div class="order-field">
        <span class="order-label"></span>
        <div class="order-value">{{picked.itemType.name}}</div>
      </div>
      <div v-if="picked.selected" class="order-field">
        <span class="order-label"></span>
        <div class="order-value">{{selectedText()}}</div>
      </div>
      <div class="order-field">
        <span class="order-label" @click = "openPicker"></span>
        
        <div class="order-value">
          <div class="time-text">{{date}}</div>
          <input type="text" id='datetime-picker' style="opacity: 0;position: absolute;right:0;width: 70%;"/>
          <i class="icon-forward"></i>
        </div>
      </div>
    </div>
    <div class="order-section second">
      <div class="order-field">
        <span class="order-label"></span>
        <div class="order-value">
          <input type="text" placeholder="" v-model="userName" class="order-field__control">
          <div class="error" v-bind:class="{'show-error':hasError('userName')}">
            <div><i class="icon-waring"></i></div>
            <div class="error-text"></div>
          </div>
        </div>
      </div>
      <div class="order-field user-info">
        <span class="order-label"></span>
        <div class="order-value">
          <input type="tel" placeholder="" v-model="mobile" maxlength="11" class="order-field__control">
          <div class="error" v-bind:class="{'show-error': hasError('mobile')}">
            <div><i class="icon-waring"></i></div>
            <div class="error-text"></div>
          </div>
        </div>
      </div>
      <div class="order-field user-info">
        <span class="order-label"></span>
        <div class="order-value">
          <input id="communityPicker" type="text" placeholder="" class="order-field__control">
          <i class="icon-forward"></i>
          <div class="error" v-bind:class="{'show-error':hasError('community')}">
            <div><i class="icon-waring"></i></div>
            <div class="error-text"></div>
          </div>
        </div>
      </div>
        <div class="order-field user-info">
        <div class="order-value">
          <input type="text" placeholder="" v-model="addressDetail" class="order-field__control">
          <div class="error" v-bind:class="{'show-error': hasError('addressDetail')}">
            <div><i class="icon-waring"></i></div>
            <div class="error-text"></div>
          </div>
        </div>
      </div>
       <div class="order-field user-info" @click="addition = !addition">
        <span class="order-label long"></span>
        <div class="">
          <i class="order-addition" v-bind:class="{active: addition}"></i>
        </div>
        </div>
        <div class="order-submit" @click="submitOrder()">
          
        </div>
      </div>
    </div>
</template>

<script>
import DatePicker from '../components/DatePicker.vue'
export default {
  name: 'order',
  components: {
    DatePicker
  },
  beforeRouteEnter (to, from, next) {
    next((vm)=>{
      if(!vm.$global.userInfo || !vm.$global.userInfo.mobile){
        next({replace: true,name: 'bindaccount'});
      }
    });
  },
  mounted(){
    var self = this;
    var now = new Date();
    var today = now.toISOString().split('T')[0];
    self.today = today;
    var todayArr= today.split('-');
    self.date = todayArr[0] + '' + todayArr[1] + '' + todayArr[2] + '' + '(09:30-18:00)';
    $("#datetime-picker").datetimePicker({
        yearSplit: '',
        monthSplit: '',
        dateSplit: '',
        min: today,
        times: function () {
          return [
            {
              values: ['(09:30-12:00)', '(13:00-18:00)']
            }
          ];
        },
        title: '',
        value: today + (now.getHours() <  11 ? ' (09:30-12:00)' : ' (13:00-18:00)'),
        onClose: function (e) {
          var values = e.value;
          self.date = values[0]+''+values[1]+''+values[2] +' ' + values[3];
          self.dataValues = values;
        }
      });
      this.$http.post('public/api/customer/get_user_info_by_token.html',{
        "token":this.$global.userInfo.token
      })
      .then(response => {
        if(response.data && response.data.data){
            this.$global.userInfo.more = response.data.data.more;
            if(this.$global.userInfo.more){
            this.mobile = this.$global.userInfo.more.mobile || '';
            this.userName = this.$global.userInfo.more.name || '';
            if(this.$global.userInfo.more.addr){
              var addrArr = this.$global.userInfo.more.addr.split(' ');
              this.addressDetail = addrArr[2];
              this.community = [addrArr[0], addrArr[1]].join(' ');
              $("#communityPicker").val(this.community);
            }
          }

        this.$http.post('http://arcfun.0lz.net/public/api/common/getArea.html').then(function(result){
            const area = result.data.data;
            self.area = area;
            var picker = $("#communityPicker").picker({
              title: "",
              cols: [
                {
                  textAlign: 'left',
                  values: area[0].items.map(function(item){return item.name})
                },
                {
                  textAlign: 'left',
                  values: area[0].items[0].items.map(function(item){return item.name})
                }
              ],
              onClose: function (e) {
                self.area_id = area[0].items[e.cols[0].activeIndex].items[e.cols[1].activeIndex].id;
                self.community = e.value.join(' ');
              }
          });
        }).catch(function(error){
            console.log(error);
        });
        }
      })
      .catch((error) => {
        console.log(error);
      });
      



  },
  methods:{
    getAreaId(){
      let names = this.community.split(' ');
      let array = this.area[0].items;
      let id;
      console.log(JSON.stringify(array));
      for (let index = 0; index < array.length; index++) {
        if (names[0] === array[index].name){
          let items = array[index].items
          for (let index = 0; index < items.length; index++) {
            if(names[1] === items[index].name){
              id = items[index].id;
              break;
            };
          }
          break;
        }
      }
      return id;
    },
    openPicker() {
        this.$refs.picker.open();
    },
    onCommunityPickerChange(picker, values) {
      picker.setColumnValues(1, communities[values[0]]);
    },
    onCommunityPickerConfirm(value, index){
      this.showCommunityPicker = false;
      this.community = value.join(' ');
    },
    hasError(prop){
      return this.errors[prop];
    },
    selectedText(separator){
      if(!this.picked.selected){
        return '';
      }
      return this.picked.selected.map(element => {
        return element.name;
      }).join(separator || ' ');
    },
    triggerTimePicker(){
      const elem = this.$refs.timePicker
            elem.click()
    },
    formateDate(){
      if(this.date){
        return new Date(this.date).toLocaleString();
      }
    },
    submitOrder(){
      if((this.dataValues[0] == '2018' && this.dataValues[1] == '09' && this.dataValues[2] == '24') || (this.dataValues[0] == '2018' && this.dataValues[1] == '10' && (this.dataValues[2] == '01' || this.dataValues[2] == '02' || this.dataValues[2] == '03'))){
        this.$toast(',');
        return;
      }
      console.log(this.dataValues);
      console.log(this.today);
      if(this.dataValues.length && new Date(this.dataValues[0] + '-' + this.dataValues[1] + '-' + this.dataValues[2]) < new Date(this.today)){
        this.$toast('');
        return;
      }
      if(!this.userName){
        this.$set(this.errors,'userName',true);
        return;
      }else{
        this.$set(this.errors,'userName',false);
      }
      if(!this.mobile || this.mobile.length !== 11){
        this.$set(this.errors,'mobile',true);
        return;
      }else{
        this.$set(this.errors,'mobile',false);
      }
      if(!this.community){
        this.$set(this.errors,'community',true);
        return;
      }else{
        this.$set(this.errors,'community',false);
      }
      if(!this.addressDetail){
        this.$set(this.errors,'addressDetail',true);
        return;
      }else{
        this.$set(this.errors,'addressDetail',false);
      }
      if(!this.area_id){
        this.area_id = this.getAreaId();
      }
      let pickedText
      let selectedText = this.selectedText('');
      if (selectedText){
        pickedText = this.picked.itemType.name +'' + this.selectedText('');
      }else{
        pickedText = this.picked.itemType.name;
      }
      
        this.$http.post('public/api/customer/addPreOrder.html',{
          time: this.dataValues.length ? this.dataValues[0]+''+this.dataValues[1]+''+this.dataValues[2] +' ' + this.dataValues[3] : this.date,
          name: this.userName,
          phone: this.mobile,
          addr: this.community + ' '+ this.addressDetail,
          area_id: this.area_id,
          goods: this.picked.selected,
          addition: this.addition ? 1 : 0,
          token: this.$global.userInfo.token
          }
      ).then(()=>{
          this.$router.replace({name:'response'});
          this.$global.selected = [];
          this.$global.itemType =null;
      }).catch(error=>{
        this.$toast('');
      });
    }
  },
  data() {
    return {
      errors:{},
      addition: false,
      mobile: '',
      community:'',
      area: [],
      area_id: null,
      addressDetail:'',
      userName:'',
      date: null,
      dataValues: [],
      picked: {
        selected: this.$global.selected,
        itemType: this.$global.itemType
      }
    }
  }
}
</script>

