import Price from './views/Price.vue'
import Test from './views/test.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: '',
        auth: true
      }
    },
    {
      path: '/pick',
      name: 'pick',
      component: Pick,
      meta: {
        title: '',
        keepAlive: false
      }
    },
    {
      path: '/multipick',
      name: 'multipick',
      component: MultiPick,
      meta: {
        title: '',
        keepAlive: false
      }
    },
    {
      path: '/order',
      name: 'order',
      component: Order,
      meta: {
        title: '',
        auth: true
      }
    },
    {
      path: '/myorder',
      name: 'myorder',
      component: MyOrder,
      meta: {
        title: '',
        auth: true
      }
    },
    {
      path: '/response',
      name: 'response',
      component: Response,
      meta: {
        title: '',
        auth: true
      }
    },
    {
      path: '/bindaccount',
      name: 'bindaccount',
      component: BindAccount,
      meta: {
        title: '',
        auth: true
      }
    },
    {
      path: '/price',
      name: 'price',
      component: Price,
      meta: {
        auth: false
      }
    },
    {
      path: '/test',
      name: 'test',
      component: Test,
      meta: {
        title: ''
      }
    }
  ]
})
